:root {
  --color: #000;
  --accent-color: #000;
  --surface-color: rgba(0, 0, 0, 0);
  --background-color: #ffffff;
  --grid-width: 1100px;
  --default-color: #000;
  --default-accent-color: #000;
  --font-family: montserrat;
  --secondary-font-family: Inter;
}

html, body {
  padding: 0;
  margin:0;
  margin-top: 50px;
  background: #fff;
  color: #0e0e0e;
  width: 100%;
  height: 100%;
}
body {
  color: var(--color, #000);
  font-family: var(--font-family);
}


.topbar{
    top:0; left:0;
    position:fixed;
    width:100%;
    padding:20px;
    margin-left: auto;
    margin-right: auto;
}

#logo{
    padding:10px;
    margin:0;
    background: blue;
}

.current{
    color:#aaa;
}

nav a{
    transition: color 0.5s;
    text-decoration:none;
    color: black;
    padding:20px;
    font-weight:bold;
}
nav a:hover{
    color: #808080;
}

nav{
    padding:5px;
    margin-right:60px;
    float:right;
}

.tag {
    background: gray;
    font-size: 8pt;
    color: #101010;
    padding: 3px 8px 3px 8px;
    text-align: center;
    text-decoration: none;
    border-radius: 3px;
    transition: background 0.5s;
}

.tag:hover {
    background: white;
}

#tags{
    background: red;
    margin-left: auto;
    margin-right: auto;
}

#content{
    max-width:500px;
    background: blue;
    margin-left: auto;
    margin-right: auto;
}

#post{
    max-width:500px;
    background: teal;
    margin-left: auto;
    margin-right: auto;
}

